@import "./landingPage";
@import "./navbar";
@import "./home";

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Verdana, Helvetica, -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
}

html {
  background-color: white;
}

a:hover,
a:visited,
a:active,
a {
  text-decoration: none;
  color: inherit;
}
