.LandingPage {
  min-height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .logo {
    width: 300px;
  }

  // Nested Component
  .SignInForm {
    width: 320px;
    padding-top: 10px;

    background-color: white;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    display: flex;
    flex-direction: column;
    align-items: center;
    .btn {
      width: 80%;
      margin-bottom: 2.5px;
    }
  }

  // Nested Component
  .CreateAccountForm {
    width: 320px;
    padding-top: 10px;
    margin-bottom: 25px;

    background-color: white;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    display: flex;
    flex-direction: column;
    align-items: center;
    .btn {
      width: 80%;
      margin-bottom: 2.5px;
    }
  }
}
